import { Injectable } from '@angular/core';
import Contacts from '../model/Contacts';
import ContactItem from '../model/ContactItem';

@Injectable({
  providedIn: 'root'
})
export class ItemsService {

  contacts    : Contacts;
  name        : string;
  number      : any;
  address     : any;
  warning_user    : number;
  warning_address : number;
  warning_phone   : number;
  searchIndex     : number;
  emptyField      : number;
  search_symbol   : number;
  
  constructor() {
    this.contacts = new Contacts;
    this.warning_user = -1;
    this.warning_address = -1;
    this.warning_phone = -1;
    this.searchIndex = -1;
    this.search_symbol = -1;
  }

  addItem(name, number, address){
    this.name = name;
    this.number = number; 
    this.address = address;

    if(this.name !== '' && this.number !== '' && this.address != ''){
     let item = new ContactItem(name, number, address);
     this.contacts.add(item);
     this.warning_user = -1;
     this.warning_phone = -1;
     this.warning_address = -1;
     this.name = '';
     this.number = '';
     this.address = '';
    } else {
      this.name = name;
      this.number = number;
      this.address = address;
      if(this.name == '') this.warning_user = 1;
      if(this.number == '') this.warning_phone = 1;
      if(this.address == '') this.warning_address = 1;
    }
  }

  deleteItem(item){
    this.contacts.delete(item);
  }

  searchSymbol(){
    this.searchIndex = 1;
  }

  searchWithField(searchQuery){   

    let result = [];

    for (let i = 0; i < this.contacts.items.length; i++) {
      if(this.contacts.items[i].name.toLowerCase().includes(searchQuery.toLowerCase())){
        result.push(this.contacts.items[i]);
      }     
    }

    this.search_symbol = 1;

    this.contacts.items = result;
  }


  search_with_field(searchQuery, event){
    if(event.keyCode == 8){
      this.contacts.items = this.contacts.loadFromLocalStorage();
      this.searchWithField(searchQuery)
    }
  }


}

