class ContactItem {

    id      : string;
    name    : string;
    number  : number;
    address : any;
    color   : any;


    constructor(name, number, address){
        this.id = Math.random().toString(36).substring(7);
        this.name = name;
        this.number = number;
        this.address = address;
        this.color = `rgb(${Math.floor(Math.random()*128 + 128)},${Math.floor(Math.random()*128 + 128)}, ${Math.floor(Math.random()*128 + 128)})`;
    }

    static fromJSON (json){
        let contactItem = new ContactItem(json.name, json.number, json.address);
        contactItem.id = json.id;
        contactItem.color = json.color;

        return contactItem;
    }
}

export default ContactItem;